<template>
	<view class="content">
		<view class="wrap">
			<view  class="un-login" v-if="!isLogin">
				<text class="un-login-text">未登录, 请登录</text>
			</view>
			<view class="has-login" v-else>
				<text class="has-login-text">{{userInfo.message}}</text>
			</view>
			<button class="btn" type="warn" @click="handler">{{isLogin ? '退出登录' : '登录'}}</button>
		</view>
	</view>
</template>

<script>
	import { mapState, mapMutations } from "vuex";
	export default {
		computed: mapState(['isLogin', 'userInfo']),
		data() {
			return {
				title: '首页'
			}
		},
		onLoad() {
		},
		methods: {
			...mapMutations(['logout']),
			handler(){
				if(this.isLogin){
					uni.showToast({
						title: '已退出登录',
					})
					this.logout()
				}else{
					uni.navigateTo({
						url:'/pages/profile/login'
					})
				}
			}
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	
	.wrap {
		margin-top: 10%;
	}
	.un-login,.has-login {
		margin-bottom: 20px;
	}
</style>
